<template>
  <v-row justify="center">
    <v-dialog v-model="state.dialogCreate" persistent fullscreen scrollable hide-overlay transition="dialog-bottom-transition">
      <v-card tile>
        <v-toolbar fixed dark color="primary">
          <v-btn icon dark @click="state.dialogCreate = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Create Blog</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn color="yellow" text :loading="state.loading" @click="save(form)">
              SAVE
            </v-btn>
            <v-btn color="white" text :loading="state.loading" @click="state.dialogCreate = false">
              CLOSE
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text>
          <v-container>
            <v-row dense class="mt-10">
              <v-col cols="12" md="12">
                <v-text-field label="Judul" v-model="form.TitleName" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="6" mb="0">
                <v-text-field label="Kategori" v-model="form.Category" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field label="Tag" v-model="form.Tags" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="12">
                <label for="">Note</label>
                <tiptap-vuetify
                  v-model="form.Blog_Description"
                  :extensions="extensions"
                  outlined
                  placeholder="Write something …"
                />
              </v-col>
              <v-col cols="12" md="12">
                <center><v-img class="pt-8" :src="form.ImagesPath" max-width="200px"></v-img></center>
                <v-file-input @change="createBase64Image" color="deep-purple accent-4" counter accept="image/*"
                  label="Upload Image" placeholder="Select your files" prepend-icon="mdi-paperclip" outlined
                  :show-size="1000">
                  <template v-slot:selection="{ index, text }">
                    <v-chip v-if="index < 2" color="deep-purple accent-4" dark label small>
                      {{ text }}
                    </v-chip>

                    <span v-else-if="index === 2" class="text-overline grey--text text--darken-3 mx-2">
                      +{{ files.length - 2 }} File(s)
                    </span>
                  </template>
                </v-file-input>
              </v-col>
              <v-col cols="12" md="6" mb="0">
                <v-text-field label="Di Tulis Oleh." v-model="form.WritenBy" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-select v-model="form.isactive" :items="items" item-text="text" item-value="value" label="Status"
                  outlined></v-select>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <div style="flex: 1 1 auto;"></div>
      </v-card>
    </v-dialog>

  </v-row>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import Utils from '@/utils/util'
import { TiptapVuetify, Heading, Bold, Italic, Strike, Underline, Code, Paragraph, BulletList, OrderedList, ListItem, Link, Blockquote, HardBreak, HorizontalRule, History } from 'tiptap-vuetify'

export default {
  name: 'BlogCreate',
  components: { TiptapVuetify },
  data () {
    return {
      extensions: [
        History,
        Blockquote,
        Link,
        Underline,
        Strike,
        Italic,
        ListItem,
        BulletList,
        OrderedList,
        [Heading, {
          options: {
            levels: [1, 2, 3]
          }
        }],
        Bold,
        Code,
        HorizontalRule,
        Paragraph,
        HardBreak
      ],
      // starting editor's content
      notifications: false,
      sound: true,
      widgets: false,
      alignments: [
        'start',
        'center',
        'end'
      ],
      form: {
        ImagesPath: ''
      },
      items: [
        {
          text: 'Active', value: '1'
        },
        {
          text: 'No Active', value: '0'
        }
      ]
    }
  },
  inject: {
    theme: {
      default: { isDark: false }
    }
  },
  computed: {
    ...mapState('blog', {
      state: (state) => state
    })
  },
  methods: {
    ...mapActions('blog', [
      'save'
    ]),
    createBase64Image: function (FileObject) {
      const reader = new FileReader()
      reader.onload = (event) => {
        this.form.ImagesPath = event.target.result
      }
      reader.readAsDataURL(FileObject)
    },
    toRupiah (nominal) {
      return Utils.toRupiah(nominal)
    },
    toDateTime (tanggal) {
      return Utils.toDateTime(tanggal)
    }
  }
}
</script>
